
import { useRouter } from 'next/navigation';
import { useState } from 'react';

export default async function Home() {
  const [searchQuery, setSearchQuery] = useState('');
  const router = useRouter();

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    router.push(`/search-results/${searchQuery}`);
  };

  return (
    <main
      id="main-content"
      className="flex flex-col gap-8 row-start-2 items-center sm:items-start"
    >
      <div className="py-24 sm:py-32">
        <div className="mx-auto max-w-7xl px-6 lg:px-8">
          <div className="mx-auto max-w-2xl lg:mx-0">
            <h1 className="mt-2 text-5xl text-header font-semibold tracking-tight sm:text-7xl">
              Accessibility in Every Sip
            </h1>
            <p className="mt-8 text-pretty text-body text-lg font-medium sm:text-xl/8">
              Our mission is to make every drink accessible to everyone,
              regardless of ability. Join us in raising a glass to accessibility
              and inclusion, one sip at a time.
            </p>
            <form onSubmit={handleSubmit} className="flex gap-4">
              <input
                id="search"
                name="search"
                value={searchQuery}
                onChange={e => setSearchQuery(e.target.value)}
                type="search"
                placeholder="Search for a drink"
                className="block w-full rounded-md border-0 bg-white py-1.5 pl-10 pr-3 text-gray-900 ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm/6"
                aria-label="Search"
              />
            </form>
          </div>
        </div>
      </div>
      <footer>footer</footer>
    </main>
  );
}
